body {
  margin: 0;
  line-height: normal;
}

:root {
  /* fonts */
  --font-karla: Karla;
  --font-raleway: Raleway;
  --font-montserrat: Montserrat;

  /* font sizes */
  --font-size-xl-2: 20.2px;
  --font-size-3xs-9: 9.9px;
  --font-size-smi: 13px;
  --font-size-base: 16px;
  --font-size-xl: 20px;
  --font-size-mini: 15px;
  --font-size-lg: 18px;
  --font-size-6xs-7: 6.7px;
  --font-size-2xs-4: 10.4px;
  --font-size-mini-3: 14.3px;
  --font-size-11xl-6: 30.6px;
  --font-size-3xl: 22px;

  /* Colors */
  --color-white: #fff;
  --color-red: #ff0000;
  --color-darkgray: #b1b1b1;
  --color-black: #000;

  /* Gaps */
  --gap-3xs: 10px;
  --gap-mid: 17px;

  /* border radiuses */
  --br-6xs-6: 6.6px;
  --br-3xs: 10px;
  --br-mid-7: 17.7px;
  --br-8xs-5: 4.5px;
  --br-26xl: 45px;
}
